<template><div class="markdown-body"><h1>薄片</h1>
<p><code v-pre="">mu-chip</code> 是一种小块的用来呈现复杂实体的块，比如说日历的事件或联系人。它可以包含一张图片，一个短字符串(必要时可能被截取的字符串)，或者是其它的一些与实体对象有关的简洁的信息。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container class=\&quot;demo-chip-wrapper\&quot;&gt;\n  &lt;mu-chip class=\&quot;demo-chip\&quot;&gt;\n    default chip\n  &lt;/mu-chip&gt;\n  &lt;mu-chip class=\&quot;demo-chip\&quot; @delete=\&quot;handleClose\&quot; delete&gt;\n    delete chip\n  &lt;/mu-chip&gt;\n  &lt;mu-chip class=\&quot;demo-chip\&quot; color=\&quot;red\&quot;&gt;\n    &lt;mu-avatar :size=\&quot;32\&quot;&gt;\n      &lt;img src=\&quot;../../assets/images/uicon.jpg\&quot;&gt;\n    &lt;/mu-avatar&gt;\n    avatar chip\n  &lt;/mu-chip&gt;\n  &lt;mu-chip class=\&quot;demo-chip\&quot; color=\&quot;green\&quot; @delete=\&quot;handleClose\&quot; delete&gt;\n    &lt;mu-avatar :size=\&quot;32\&quot;&gt;\n      &lt;img src=\&quot;../../assets/images/uicon.jpg\&quot;&gt;\n    &lt;/mu-avatar&gt;\n    avatar delete chip\n  &lt;/mu-chip&gt;\n  &lt;mu-chip class=\&quot;demo-chip\&quot; color=\&quot;teal\&quot; @delete=\&quot;handleClose\&quot; delete&gt;\n    &lt;mu-avatar :size=\&quot;32\&quot; color=\&quot;blue\&quot;&gt;\n      &lt;mu-icon value=\&quot;face\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;/mu-avatar&gt;\n    icon avatar chip\n  &lt;/mu-chip&gt;\n\n  &lt;mu-chip class=\&quot;demo-chip\&quot; color=\&quot;blue300\&quot; @delete=\&quot;handleClose\&quot; delete&gt;\n    &lt;mu-avatar text-color=\&quot;blue300\&quot; color=\&quot;indigo900\&quot; :size=\&quot;32\&quot;&gt;MB&lt;/mu-avatar&gt; custom chip\n  &lt;/mu-chip&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    handleClose () {\n      window.alert('你点击了删除按钮')\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-chip-wrapper {\n  text-align: center;\n}\n.demo-chip {\n  margin: 8px;\n  vertical-align: middle;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container class="demo-chip-wrapper">
  <mu-chip class="demo-chip">
    default chip
  </mu-chip>
  <mu-chip class="demo-chip" @delete="handleClose" delete="">
    delete chip
  </mu-chip>
  <mu-chip class="demo-chip" color="red">
    <mu-avatar :size="32">
      <img src="../../assets/images/uicon.jpg">
    </mu-avatar>
    avatar chip
  </mu-chip>
  <mu-chip class="demo-chip" color="green" @delete="handleClose" delete="">
    <mu-avatar :size="32">
      <img src="../../assets/images/uicon.jpg">
    </mu-avatar>
    avatar delete chip
  </mu-chip>
  <mu-chip class="demo-chip" color="teal" @delete="handleClose" delete="">
    <mu-avatar :size="32" color="blue">
      <mu-icon value="face"></mu-icon>
    </mu-avatar>
    icon avatar chip
  </mu-chip>

  <mu-chip class="demo-chip" color="blue300" @delete="handleClose" delete="">
    <mu-avatar text-color="blue300" color="indigo900" :size="32">MB</mu-avatar> custom chip
  </mu-chip>
</mu-container>


</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip-wrapper"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-chip</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip"</span>&gt;</span>
    default chip
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-chip</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-chip</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip"</span> <span class="hljs-attr">@delete</span>=<span class="hljs-string">"handleClose"</span> <span class="hljs-attr">delete</span>&gt;</span>
    delete chip
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-chip</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-chip</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"red"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"32"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"../../assets/images/uicon.jpg"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
    avatar chip
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-chip</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-chip</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"green"</span> <span class="hljs-attr">@delete</span>=<span class="hljs-string">"handleClose"</span> <span class="hljs-attr">delete</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"32"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"../../assets/images/uicon.jpg"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
    avatar delete chip
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-chip</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-chip</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"teal"</span> <span class="hljs-attr">@delete</span>=<span class="hljs-string">"handleClose"</span> <span class="hljs-attr">delete</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"32"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"blue"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"face"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
    icon avatar chip
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-chip</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">mu-chip</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"blue300"</span> <span class="hljs-attr">@delete</span>=<span class="hljs-string">"handleClose"</span> <span class="hljs-attr">delete</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">text-color</span>=<span class="hljs-string">"blue300"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"indigo900"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"32"</span>&gt;</span>MB<span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span> custom chip
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-chip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    handleClose () {
      <span class="hljs-built_in">window</span>.alert(<span class="hljs-string">'你点击了删除按钮'</span>)
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-chip-wrapper</span> {
  <span class="hljs-attribute">text-align</span>: center;
}
<span class="hljs-selector-class">.demo-chip</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">8px</span>;
  <span class="hljs-attribute">vertical-align</span>: middle;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="chips-array"><a class="header-anchor" href="#chips-array" aria-hidden="true">¶</a> Chips Array</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container class=\&quot;demo-chip-wrapper\&quot;&gt;\n  &lt;mu-chip class=\&quot;demo-chip\&quot; v-for=\&quot;chip, index in chips\&quot; :key=\&quot;chip\&quot; :color=\&quot;chip\&quot; @delete=\&quot;remove(index)\&quot; delete&gt;{{chip}}&lt;/mu-chip&gt;\n  &lt;mu-button color=\&quot;primary\&quot; v-if=\&quot;chips.length === 0\&quot; @click=\&quot;reset\&quot;&gt;reset&lt;/mu-button&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nconst initChips = ['primary', 'secondary', 'success', 'warning', 'info', 'error'];\nexport default {\n  data () {\n    return {\n      chips: [...initChips]\n    }\n  },\n  methods: {\n    remove (index) {\n      this.chips.splice(index, 1);\n    },\n    reset () {\n      this.chips = [...initChips];\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-chip-wrapper {\n  text-align: center;\n}\n.demo-chip {\n  margin: 8px;\n  vertical-align: middle;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container class="demo-chip-wrapper">
  <mu-chip class="demo-chip" v-for="chip, index in chips" :key="chip" :color="chip" @delete="remove(index)" delete="">{{chip}}</mu-chip>
  <mu-button color="primary" v-if="chips.length === 0" @click="reset">reset</mu-button>
</mu-container>


</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>  <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip-wrapper"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-chip</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-chip"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"chip, index in chips"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"chip"</span> <span class="hljs-attr">:color</span>=<span class="hljs-string">"chip"</span> <span class="hljs-attr">@delete</span>=<span class="hljs-string">"remove(index)"</span> <span class="hljs-attr">delete</span>&gt;</span>{{chip}}<span class="hljs-tag">&lt;/<span class="hljs-name">mu-chip</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"chips.length === 0"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"reset"</span>&gt;</span>reset<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">const</span> initChips = [<span class="hljs-string">'primary'</span>, <span class="hljs-string">'secondary'</span>, <span class="hljs-string">'success'</span>, <span class="hljs-string">'warning'</span>, <span class="hljs-string">'info'</span>, <span class="hljs-string">'error'</span>];
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">chips</span>: [...initChips]
    }
  },
  <span class="hljs-attr">methods</span>: {
    remove (index) {
      <span class="hljs-keyword">this</span>.chips.splice(index, <span class="hljs-number">1</span>);
    },
    reset () {
      <span class="hljs-keyword">this</span>.chips = [...initChips];
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-chip-wrapper</span> {
  <span class="hljs-attribute">text-align</span>: center;
}
<span class="hljs-selector-class">.demo-chip</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">8px</span>;
  <span class="hljs-attribute">vertical-align</span>: middle;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="chip-props"><a class="header-anchor" href="#chip-props" aria-hidden="true">¶</a> Chip Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>chip 的颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>text-color</td>
<td>chip 文字颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>selected</td>
<td>已选中状态</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>delete</td>
<td>显示删除的图标</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="chip-events"><a class="header-anchor" href="#chip-events" aria-hidden="true">¶</a> Chip Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>delete</td>
<td>点击删除图标时触发事件</td>
<td>—</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style lang="less">
.demo-chip-wrapper {
  text-align: center;
}
.demo-chip {
  margin: 8px;
  vertical-align: middle;
}
</style>
<script>
const initChips = ['primary', 'secondary', 'success', 'warning', 'info', 'error'];
export default {
  data () {
    return {
      chips: [...initChips]
    }
  },
  methods: {
    handleClose () {
      window.alert('你点击了删除按钮')
    },
    remove (index) {
      this.chips.splice(index, 1);
    },
    reset () {
      this.chips = [...initChips];
    }
  }
}
</script>


